.login-wrapper{width:100%;height:100%;background-image:linear-gradient(73deg,#292346,#874452 50%,#be5a5a 75%)}.login-wrapper .login-content{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%,-55%);transform:translate(-50%,-55%);width:700px;height:350px;-webkit-box-shadow:8px 8px 15px 1px #444;box-shadow:8px 8px 15px 1px #444;border-radius:30px;display:grid;grid-template-columns:repeat(2,1fr);overflow:hidden}.login-wrapper .login-content input{margin-bottom:35px;border-bottom:1px solid #fff;color:#fff}.login-wrapper .login-content aside{grid-column:.5;background-image:linear-gradient(33deg,#231434,#6c3148 75%)}.login-wrapper .login-content section{grid-column:.66666667;background-image:linear-gradient(53deg,#903e51,#b84d57 50%,#d7595c 75%);display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-moz-box-orient:vertical;-moz-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:center;-webkit-justify-content:center;-moz-box-pack:center;-ms-flex-pack:center;justify-content:center;padding:0 20px}